.bubble {
    position: absolute;
    width: 40px;
    height: 40px;
    border: 1px solid white;
    box-shadow: 0px 0px 30px 10px rgba(57, 73, 220, 0.346);
    background-color: rgba(255, 255, 255, 0.346);
    border-radius: 100%;
    transform: translate(-50%, -50%);
    transition: 0.2s ease-out;
    z-index: -1;
}